@import "sass/index";
@import "../../variables.scss";

.rootPanel {
  width: fit-content;

  position: absolute;
  bottom: -10px;

  margin-top: #{$cellPanelMargin}px;
  transform-origin: 50% 50%;
  display: flex;
}

.rotated {
  transform: rotate(-90deg) scale(1) translate(-100%, 0%);
  margin-top: 0;
}

.cellPanel {
  display: flex;
  flex-direction: column;
}

.cells {
  margin-top: #{$rouletteCellGap}px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  grid-gap: #{$rouletteCellGap}px;
  gap: #{$rouletteCellGap}px;
}

.cell {
  padding: 0;
  text-align: center;
}

.rowCell {
}

.zeroCell {
  width: 100%;
  height: #{$cellWidth}px;
}
