@import "sass/index";
@import "../../variables.scss";

.root {
  display: block;
  margin-top: #{$cellWidth}px;
  position: absolute;
  top: 0;
  left: calc(-100% + #{$cellWidth}px);
  @media (max-width: $mobileScrrenSize) {
    margin-top: #{$cellWidthMobile}px;
    left: calc(-100% + #{$cellWidthMobile}px);
  }
}

.cells {
  display: grid;
  grid-template-columns: 1fr 1fr;
  grid-template-areas: "left center" "right center";
  grid-gap: #{$rouletteCellGap}px;
  margin-bottom: #{$rouletteCellGap}px;
}

.cellUnit {
  width: #{$cellWidth}px;
  white-space: nowrap;
  p {
    transform: rotate($rotate);
    font-family: "Gilroy";
    font-style: normal;
    font-weight: 900;
    font-size: 7.79336px;
    line-height: 10px;
    /* identical to box height */

    text-align: center;

    color: #ffffff;
  }
  @media (max-width: $mobileScrrenSize) {
    width: #{$cellWidthMobile}px;
  }
}

.cellCenter {
  grid-area: center;
  height: #{($cellWidth + $rouletteCellGap) * 4}px;
  @media (max-width: $mobileScrrenSize) {
    height: #{($cellWidthMobile + $rouletteCellGap) * 4}px;
  }
}

.cellLeft {
  grid-area: left;
}

.cellRight {
  grid-area: right;
}
