@import "./variables.scss";
@import "../../variables.module.scss";

.rootGame {
  margin: 0 auto;
  width: 100%;
  height: 306px;
  display: flex;
  justify-content: center;
  visibility: hidden;
  position: relative;
  padding-top: 10px;

  background: $default;
  @include backgroundOptions;
}

.rouletteRoot {
  width: fit-content;
  height: fit-content;
  margin: 0;
  position: relative;

  display: flex;
  justify-content: center;
}
.roulette {
  width: 100%;
  height: 100%;
  transform: rotate(3deg);
}
.ballContainer {
  position: absolute;

  width: 67%;
  top: 50%;
  transform: translateY(-50%);

  height: fit-content;

  display: flex;
  justify-content: flex-end;
}
.ball {
}
.visible {
  visibility: visible;
}
.cell {
  width: 100%;
  height: 100%;
  min-width: #{$cellWidth}px;
  min-height: #{$cellWidth}px;

  font-family: "Gilroy";
  font-style: normal;
  font-weight: 900;
  font-size: 7.79336px;
  line-height: 10px;
  /* identical to box height */

  text-align: center;

  color: #ffffff;
  @media (max-width: $mobileScrrenSize) {
    min-width: #{$cellWidthMobile}px;
    min-height: #{$cellWidthMobile}px;
    font-weight: 600;
  }
}
